.navbar {
    background-color: darkred;
    color: white;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
  }
  
  .content {
    padding-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .item-pair {
    padding-top: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .item {
    padding-left: 20px;
    padding-right: 20px;
  }

  .navbar-link {
    color: white;
    font-size: 20px;
    text-decoration: none;
  }
  
  .navbar-link:visited {
    color: white;
  }
  

  .selected-item {
    padding-left: 20px;
    padding-right: 20px;
    border-width: 5px;
    border-style: solid;
    border-color: red;
  }
  
  
  .navbar-list {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0;
  }
  
  .navbar-item {
    margin: 0 10px;
    padding-left: 20px;
    padding-right: 20px;
    font-family: 'Franklin Gothic', 'Arial', sans-serif;
    font-size: 20px;
  }

  .item-text {
    padding-top: 0.5rem;
    font-size: 20px;
    font-family: 'Franklin Gothic', 'Arial', sans-serif;
  }

  .home-page-text {
    padding-top: 1rem;
    margin-bottom: -1rem;
    font-size: 40px;
    font-family: 'Franklin Gothic', 'Arial', sans-serif;
    font-weight: bold;
    color: #222222;
  }
  